
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Divider from 'vux/src/components/divider/index.vue'

import Register from '@/page/Register.vue'


import api from '../axios'

export default {
components: {
XButton,
Register,
Flexbox,
FlexboxItem,
Divider
},
data () {
return {
email: '934007168@qq.com',
password: '123456',
dynamicValidateForm: {
email: '',
password: ''
},
activeName: this.$store.state.activeName
}
},
methods: {
submitForm () {
if (this.$refs.emailValid.valid) {
let opt = {
email: this.email,
password: this.password
}
api.UserLogin(opt).then(({data}) => {
if (data.success) {




this.$store.dispatch('UserLogin', data.token)
this.$store.dispatch('UserName', data.email)
let redirect = decodeURIComponent(this.$route.query.redirect || '/')
this.$router.push({
path: redirect
})
} else {




}
})
} else {
console.log('Error Submit')
return false
}
}
}
}



